.ProCluster{
    border-style: ridge  ;
    border-width: 10px;
    border-color: rgba(30, 30, 65, 0.973);
    background-color: black;
    padding: 10px;
    width: 30vw;
    margin-left: 2vw;
}

.proGrid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 15px;
    }

    @media screen and (max-width: 700px) {
        .proGrid {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(2, 1fr);
            grid-column-gap: 10px;
            grid-row-gap: 15px;
            } 
        .ProCluster {
            width: 45vw;
        }     
    }

    @media screen and (max-width: 460px) {
        .proGrid {
            display: grid;
            grid-template-columns: repeat(1, 1fr);
            grid-template-rows: repeat(3, 1fr);
            grid-column-gap: 10px;
            grid-row-gap: 15px;
            } 
        .ProCluster {
            width: 80vw;
        }
        .proImage{
            width: 80%;
            height: auto;
            margin: auto 10%;
        }     
    }

