/* -----------stars and clouds effect------------ */
@keyframes move-twink-back {
    from {background-position:0 0;}
    to {background-position:-10000px 5000px;}
}
@-webkit-keyframes move-twink-back {
    from {background-position:0 0;}
    to {background-position:-10000px 5000px;}
}
@keyframes move-clouds-back {
    from {background-position:0 0;}
    to {background-position:10000px 0;}
}
@-webkit-keyframes move-clouds-back {
    from {background-position:0 0;}
    to {background-position:10000px 0;}
}
.stars, .twinkling, .clouds {
  position: relative;;
  top:0;
  left:0;
  width:100vw;
  height:100vh;
}
.clouds{
    height: auto;
    position: absolute;
    top: 0%;
}
.stars {
    background:#000 url(/static/media/stars2.430ac91b.png) repeat top center;
    z-index:0;
  }
  .twinkling{
    background:transparent url(data:image/png;base64,) repeat top center;
    z-index:1;
    -webkit-animation:move-twink-back 200s linear infinite;
    animation:move-twink-back 200s linear infinite;
  }
.clouds{
    background: url(/static/media/clouds6wave.5b720d0e.png) repeat top center;
    background-size: 100% 100%;
    background-repeat: repeat-x;
    height: 110%;
    z-index:3;
  -webkit-animation:move-clouds-back 1200s linear infinite;
  animation:move-clouds-back 1200s linear infinite;
}


